import React from 'react';
import { useLocation, useParams } from 'react-router';
import { Link } from 'react-router-dom';
import '../Manager/teammember.css'
const CRUDteam = (props) => {
    let id = useLocation().search
    const idp = new URLSearchParams(id).get('id')
    console.log(idp)
    const incoData = JSON.parse(localStorage.getItem('TeamStock'))
    let temp;
    const data=()=>{
        for(let i=0;i<incoData.length;i++){
            if(incoData[i].id==idp){
                temp=incoData[i];
                break;
            }
        }
    }
    console.log(temp)
    const handleDelete = ()=>{
        let j ;
        for(let i=0;i<incoData.length;i++){
            if(incoData[i].id==idp){
                j=i;
                break;
            }
        }
        localStorage.setItem(
            "TeamStock",
            JSON.stringify(
              incoData.slice(0, j).concat(incoData.slice(j + 1))
            )
          );
    }
    const handleUpdate = ()=>{
        let tempUpdate;
        let i;
        for(i=0;i<incoData.length;i++){
            if(incoData[i].id==idp){
                tempUpdate =incoData[i];
                break;
            }
        }
       var obj1 =  {
            id:tempUpdate.id,
            First_name:document.getElementById("fName").innerText,
            Second_name:document.getElementById("sName").innerText,
            DOB:document.getElementById("dob").innerText,
            Gender:document.getElementById("gender").innerText,
            Exp:document.getElementById("exp").innerText,
            Password:document.getElementById("pass").innerText,
            Avatar: "fas fa-user-tie",
            img: "https://www.sasscaffold.com.au/wp-content/uploads/2019/10/pngtree-business-male-user-avatar-vector-png-image_1511454.jpg"
        }
        incoData[i]=obj1
        localStorage.setItem("TeamStock",JSON.stringify(incoData))
    }

   
    return ( 
        <div id='maindiv'>
            {data()};
            <nav id='teammemnav'>
                <div>
                    <h1 id="welcome">Welcome</h1>
                    <h3 id='name'>Shubham Upadhyay</h3>
                </div>
                <div id="desdiv">
                    <h3 id="des">Store Manager</h3>
                    <i class="fas fa-user-tie"></i>
                </div>
            </nav>
            <div id='formdiv'>
                <h1>Manage Sales Executive</h1>
                <form>
                    <p id='fName'  contentEditable='true'>{temp.First_name}</p>
                    <p id='sName' contentEditable>{temp.Second_name} </p>
                    <p id='dob'  contentEditable='true'>{temp.DOB}</p>
                    <p id='gender'  contentEditable>{temp.Gender}</p>
                    <p id='exp' contentEditable>{temp.Exp}</p>
                    <p id='pass' contentEditable>{temp.Password}</p>
                  
                </form>
                <div id='buttonDiv'>
                <Link to='/myteam' onClick={()=>handleUpdate()}><button class='buttonClass4' id='update'>Update</button></Link>
                <Link to='/myteam' onClick={()=>handleDelete()}><button class='buttonClass4' id='delete'>Delete</button></Link>
                </div>
            </div>
        </div>
     );
}
 
export default CRUDteam;
