import React from 'react';
import { HashRouter, Route, Switch} from 'react-router-dom';
import ManageOrder from '../src/components/Manager/manageOrder';
import CRUDteam from '../src/components/Manager/teammember';
import NewOrder from '../src/components/Manager/NewOrder';
import Logins from './components/logins';
import SalesHome from '../src/components/Executive';
import NewOrderExe from '../src/components/Executive/ChangeOrder';
import CRUDorder from '../src/components/Manager/CRUDorder';
import StoreManager from '../src/components/Manager/managerHome';
import InvManage from './components/Manager/medicineStore';
import CRUDmed from './components/Manager/MedCRUD';
import EditTeam from './components/Manager/teamHome';

const Home = () => {
  return ( 
    <HashRouter>
      <div>
        <Switch>
          <Route path="/storemanager" component={StoreManager}></Route>
          <Route path ='/sales' component={SalesHome}/>
          <Route path ='/medInv' component={InvManage}/>
          <Route path='/myteam' component={EditTeam}/>
          <Route path='/order' component={ManageOrder}/>
          <Route path='/CRUDmed' component={CRUDmed}/>
          <Route path='/CRUDteam' component={CRUDteam}/>
          <Route path='/CRUDorder' component={CRUDorder}/>
          <Route path='/NewOrder' component={NewOrder}/>
          <Route path='/exeordupexe' component={NewOrderExe}/>
          <Route path='/start' component={Logins}/>
          <Logins></Logins>
        </Switch>
  </div>
  </HashRouter>);
}
 
export default Home;