import React from 'react';
import { Link } from 'react-router-dom';
const SalesHome = () => {
    let product = JSON.parse(localStorage.getItem('orderStock'))
    return ( 
        <div id='maindiv'>
           <nav>
                <div>
                    <h1 id="welcome">Welcome</h1>
                    <h3 id='name'>Akash Gupta</h3>
                </div>
                <div id="desdiv">
                    <h3 id="des">Sales Executive</h3>
                    <i class="fas fa-user-tie"></i>
                </div>
            </nav>
            <div id='content'>
                <h1>Orders</h1>
                <Link to = '/exeordupexe'><button class='buttonClass3'>NEW ORDER</button></Link>
                <div id='ordercontainer'>
                {product.map(item => <OrderSet {...item}></OrderSet>)}
                </div>
                </div>
        </div>
     );
}
 
export default SalesHome;
const OrderSet = ({id,name,mobile,products,total,createdby}) => {
    return (   <div id='orderDetail'>
        <h3>Order No -<span>{id}</span></h3>
        <h3>Customer Name - <span>{name}</span></h3>
        <h3>Mob: {mobile}</h3>
        {products.map(item => <ProSet {...item}></ProSet>)}
        <h3>Total-<span>Rs. {total}</span></h3>
    </div> 
     );
}
const ProSet = ({mediName,quantity}) => {
    return ( 
        <h3 id="product">
         <span>{mediName}</span><span>{quantity}</span>
        </h3>
     );
}
 