import React from "react";
import { useLocation, useParams } from "react-router";
import { Link } from "react-router-dom";
import medCrudCSS from "../MedCRUD/index.module.css";
const CRUDmed = (props) => {
  let id = useLocation().search;
  const idp = new URLSearchParams(id).get("id");
  console.log(idp);
  const incoData = JSON.parse(localStorage.getItem("MedicineStock"));
  let temp;
  const data = () => {
    for (let i = 0; i < incoData.length; i++) {
      if (incoData[i].id == idp) {
        temp = incoData[i];
        break;
      }
    }
  };
  console.log(temp);
  const handleDelete = () => {
    let j;
    for (let i = 0; i < incoData.length; i++) {
      if (incoData[i].id == idp) {
        j = i;
        break;
      }
    }
    localStorage.setItem(
      "MedicineStock",
      JSON.stringify(incoData.slice(0, j).concat(incoData.slice(j + 1)))
    );
  };
  const handleUpdate = () => {
    let tempUpdate;
    let i;
    for (i = 0; i < incoData.length; i++) {
      if (incoData[i].id == idp) {
        tempUpdate = incoData[i];
        break;
      }
    }
    var obj1 = {
      id: tempUpdate.id,
      Name: document.getElementById("medName1").innerText,
      MafName: document.getElementById("mafName1").innerText,
      Price: document.getElementById("price1").innerText,
      Discount: document.getElementById("discount1").innerText,
      Stock: document.getElementById("stock1").innerText,
    };
    incoData[i] = obj1;
    localStorage.setItem("MedicineStock", JSON.stringify(incoData));
  };

  return (
    <div className={medCrudCSS.maindiv}>
      {data()}
      <nav className={medCrudCSS.mnginvnav}>
        <div>
          <div>
            <h1 id="welcome">Welcome</h1>
            <h3 id="name">Admin</h3>
          </div>
          <h2>Inventory Management</h2>
        </div>

        <div className={medCrudCSS.desdiv}>
          <Link to="/start">
            {" "}
            <button className={medCrudCSS.buttonClass4}>Log Out</button>{" "}
          </Link>
          <h3 id="des">Store Manager</h3>
          <Link
            to="/storemanager"
            style={{ fontSize: "50px", color: "yellow" }}
          >
            <i class="fas fa-user-tie"></i>
          </Link>
        </div>
      </nav>
      <div className={medCrudCSS.formdiv}>
        <h1>Manage Medicine</h1>
        <form>
          <p
            id="medName1"
            className={medCrudCSS.medName1}
            contentEditable="true"
          >
            {temp.Name}
          </p>
          <p id="mafName1" className={medCrudCSS.mafName1} contentEditable>
            {temp.MafName}{" "}
          </p>
          <p id="stock1" className={medCrudCSS.stock1} contentEditable="true">
            {temp.Stock}
          </p>
          <p id="price1" className={medCrudCSS.price1} contentEditable>
            {temp.Price}
          </p>
          <p id="discount1" className={medCrudCSS.discount1} contentEditable>
            {temp.Stock}
          </p>
        </form>
        <div className={medCrudCSS.buttonDiv}>
          <Link to="/medInv" onClick={() => handleUpdate()}>
            <button className={medCrudCSS.buttonClass4} id="update">
              Update
            </button>
          </Link>
          <Link to="/medInv" onClick={() => handleDelete()}>
            <button className={medCrudCSS.buttonClass4} id="delete">
              Delete
            </button>
          </Link>
        </div>
      </div>
    </div>
  );
};

export default CRUDmed;
