import React from 'react';
import { Link } from 'react-router-dom';
import { useLocation } from 'react-router';
import { useState } from 'react';
const NewOrderExe = (props) => {
    const [Refresh, setRefresh] = useState(0);
    const [productArr, setproductArr] = useState([])
    let medData = JSON.parse(localStorage.getItem("MedicineStock"))
    const handletotalmed = () => {
        let y = document.getElementsByClassName('dataget');
        let y1 = y.length;
        let out = [];
        for (let i = 0; i < y1; i++) out.push({ mediName: y[i].children[0].innerHTML, quantity: y[i].children[3].innerHTML })
        setproductArr(out)
        alert("after render of hook")
        handleTotalAmount();
        document.getElementById('addMed').style.display = 'none'
    }
    const handleTotalAmount =()=>{
        let sum=0;
        for(let i=0;i<productArr.length;i++)
        {
            let price=0;
            for(let j=0;j<medData.length;j++){
                 if(productArr[i].mediName===medData[j].Name)
                 {
                    price=parseInt(medData[i].Price);
                    break
                 }
            }
            sum=sum+(parseInt(productArr[i].quantity)*price)
        }
       return sum;
    }
    const sendData = ()=>{
        var ax = JSON.parse(localStorage.getItem('orderStock'))
        let a = new Date()
        var xy  = {
            id: a.getTime(),
            name: document.getElementById('Name').value,
            mobile:document.getElementById('moblie').value,
            products:productArr,
            total:document.getElementById('total').innerText
        } 
        ax.push(xy)
        localStorage.setItem('orderStock',JSON.stringify(ax))
    }
    return (
        <div id='maindiv'>
            <nav>
                <div>
                    <h1 id="welcome">Welcome</h1>
                    <h3 id='name'>Akash Gupta</h3>
                </div>
                <div id="desdiv">
                    <h3 id="des">Sales Manager</h3>
                    <i class="fas fa-user-tie"></i>
                </div>
            </nav>
            <div id='formdiv'>
                <h1>Create Order</h1>
                <h4>Order Number <span id='orderNo'></span></h4>
                <form>
                    <input type={Text} id='Name' placeholder='Name'></input>
                    <input type={Text} id='moblie' placeholder='Mobile'></input>
                    {productArr.map(item => <Prod {...item}></Prod>)}
                    <p id='total'>Rs.{handleTotalAmount()}</p>
                </form>
                <div id='buttonDiv'>
                    <buttton id="addmed" className="buttonClass4" onClick={() => {
                        document.getElementById('addMed').style.display = 'flex'
                    }}> Add Medicine </buttton>
                   <Link to='/sales'> <button id='Confirm Order' className="buttonClass4" onClick={()=>sendData()}> Confirm</button></Link>
                </div>
            </div>
            <div id='addMed'>
                <h2>MEDICINE LIST</h2>
                <div id="hold">
                    {medData.map(item => <MedCard {...item}></MedCard>)}
                </div>
                <buttton id="addmed" className="buttonClass4" onClick={() => handletotalmed()}> ADD</buttton>
            </div>
        </div>
    );
}

export default NewOrderExe;
const MedCard = ({ id, Name, Price }) => {
    return (
        <div id="medhold" className="dataget">
            <p>{Name}</p>
            <p>{Price}</p>
            <span id='minus' onClick={(e) => {
                let y = parseInt((e.target.parentElement.childNodes[3].childNodes[0].data));
                if (y > 0) {
                    y = y - 1;
                    e.target.parentElement.childNodes[3].childNodes[0].data = y;
                }
                else {
                    alert('Cant be less than 0')
                }
            }}>-</span>
            <span>0</span>
            <span id='plus' onClick={(e) => {
                let y = parseInt((e.target.parentElement.childNodes[3].childNodes[0].data));
                y = y + 1;
                e.target.parentElement.childNodes[3].childNodes[0].data = y;
            }}>+</span>
        </div>
    );
}
const Prod = ({ mediName, quantity }) => {
    return (<h3><span>{mediName}</span><span>{quantity}</span></h3>);
}

