import React from "react";
import { useState } from "react";
import { Link } from "react-router-dom";
import teamCSS from "../teamHome/index.module.css";
const EditTeam = () => {
  let IncomingTeam = JSON.parse(localStorage.getItem("TeamStock"));
  const handleTeamAdd = () => {
    let f = new Date();
    let temp = {
      id: f.getTime(),
      First_name: document.getElementById("firstname").value,
      Second_name: document.getElementById("secondname").value,
      Avatar: "fas fa-user-tie",
      DOB: document.getElementById("dob").value,
      Gender: document.getElementById("gender").value,
      Exp: document.getElementById("experience").value,
      Password: document.getElementById("pass").value,
      img: "https://www.sasscaffold.com.au/wp-content/uploads/2019/10/pngtree-business-male-user-avatar-vector-png-image_1511454.jpg",
    };
    IncomingTeam.push(temp);
    localStorage.setItem("TeamStock", JSON.stringify(IncomingTeam));
    settotallitem(totallitem + 1);
    document.getElementById("addMember").style.display = "none";
  };
  const [totallitem, settotallitem] = useState(IncomingTeam.length);
  return (
    <div className={teamCSS.maindiv}>
      <nav className={teamCSS.mnginvnav}>
        <div>
          <div>
            <h1 id="welcome">Welcome</h1>
            <h3 id="name">Admin</h3>
          </div>
          <h2>Team Management</h2>
        </div>

        <div className={teamCSS.head}>
          <button
            className={teamCSS.buttonClassaz}
            onClick={() =>
              (document.getElementById("addMember").style.display = "flex")
            }
          >
            ADD ITEM
          </button>
        </div>

        <div className={teamCSS.desdiv}>
          <Link to="/start">
            {" "}
            <button className={teamCSS.buttonClassaz}>Log Out</button>{" "}
          </Link>
          <h3 id="des">Store Manager</h3>
          <Link
            to="/storemanager"
            style={{ fontSize: "50px", color: "yellow" }}
          >
            <i class="fas fa-user-tie"></i>
          </Link>
        </div>
      </nav>
      <div className={teamCSS.contentArea}>
        <div id="addMember" className={teamCSS.addMember}>
          <input
            type={Text}
            placeholder="First Name"
             className={teamCSS.addform}
            id="firstname"
          ></input>
          <input
            type={Text}
            placeholder="Second Name"
             className={teamCSS.addform}
            id="secondname"
          ></input>
          <input
            type={Text}
            placeholder="Gender"
             className={teamCSS.addform}
            id="gender"
          ></input>
          <input
            type={Text}
            placeholder="Date of Birth"
             className={teamCSS.addform}
            id="dob"
          ></input>
          <input
            type={Text}
            placeholder="Experience"
             className={teamCSS.addform}
            id="experience"
          ></input>
          <input
            type={"password"}
            placeholder="Password"
            className={teamCSS.addform}
            id="pass"
          ></input>
            <div>
            <button onClick={() => handleTeamAdd()} className={teamCSS.buttonClass2}>
            ADD MEMBER
          </button>
          <button
             className={teamCSS.buttonClass2}
            onClick={() =>
              (document.getElementById("addMember").style.display = "none")
            }
          >
            close
          </button>
            </div>
        </div>
        <div className={teamCSS.TeamHolder}>
          {IncomingTeam.map((item) => (
            <Team {...item}></Team>
          ))}
        </div>
      </div>
    </div>
  );
};

export default EditTeam;


const Team = ({
  id,
  First_name,
  Second_name,
  Gender,
  Exp,
  img,
  Password,
  DOB,
}) => {
  let linky = "/CRUDteam" + "?" + "id=" + id;
  return (
    <Link to={linky} style={{width:"24%",margin:"3% 15px"}} >
      <div className={teamCSS.TeamDetail}>
        <div className={teamCSS.teamImg}>
          <img src={img} />
        </div>
        <div className={teamCSS.memdetail}>
          <h3>
            <span id="firstName" className={teamCSS.firstName}>{First_name}</span>
            <span>{Second_name}</span>
          </h3>
          <h4>{Gender}</h4>
          <h4>DOB: {DOB}</h4>
          <h4>Exp Years:  {Exp} </h4>
          <h4>{Password}</h4>
        </div>
      </div>
    </Link>
  );
};
