import React from "react";
import { useState } from "react";
import { Link } from "react-router-dom";
import medCSS from "../medicineStore/index.module.css";
const InvManage = () => {
  let IncomingMedicine = JSON.parse(localStorage.getItem("MedicineStock"));
  const [totallitem, settotallitem] = useState(IncomingMedicine.length);
  const handleADDgo = () => {
    let a=document.getElementById("addMediname").value;
    let b=document.getElementById("addMediManufacturer").value
    let c= document.getElementById("addMediPrice").value
    let d=document.getElementById("addMediDiscount").value
    let e= document.getElementById("addMediStock").value
    let f = new Date();
    if(a===""||b===""|| c===""||d===""||e==="")
     alert("please fill all fields")
     else{
        let temp = {
            id: f.getTime(),
            Name:a ,
            MafName:b ,
            Price:c,
            Discount: d,
            Stock:e,
          };
          IncomingMedicine.push(temp);
          localStorage.setItem("MedicineStock", JSON.stringify(IncomingMedicine));

          settotallitem(totallitem + 1);
          document.getElementById("Medadditem").style.display = "none";
        };
     }
  return (
    <div className={medCSS.maindiv}>

      <nav className={medCSS.mnginvnav}>

        <div>
            <div>
            <h1 id="welcome">Welcome</h1>
             <h3 id="name">Admin</h3>
            </div>
          <h2>Inventory Management</h2>
        </div>
        
        <div className={medCSS.head} >
          <button
             className={medCSS.buttonClassbz}
            onClick={() =>
              (document.getElementById("Medadditem").style.display = "flex")
            }
          >
            ADD ITEM
          </button>
        </div>

        <div className={medCSS.desdiv}>
          <Link to="/start">
            {" "}
            <button className={medCSS.buttonClassbz}>Log Out</button>{" "}
          </Link>
          <h3 id="des">Store Manager</h3>
          <Link to="/storemanager" style={{fontSize:"50px",color:"yellow"}}><i class="fas fa-user-tie" ></i></Link>
        </div>

      </nav>

      <div className={medCSS.contentArea}>
        <div id="Medadditem" className={medCSS.Medadditem}>
          <input
            id="addMediname"
            type={Text}
            placeholder="Name"
            className={medCSS.addform}
          ></input>
          <input
            id="addMediManufacturer"
            type={Text}
            placeholder="Manufacturer"
            className={medCSS.addform}
          ></input>
          <input
            id="addMediPrice"
            type={Text}
            placeholder="Price"
            className={medCSS.addform}
          ></input>
          <input
            id="addMediDiscount"
            type={Text}
            placeholder="Discount"
            className={medCSS.addform}
          ></input>
          <input
            id="addMediStock"
            type={Text}
            placeholder="Stock"
            className={medCSS.addform}
          ></input>
          <div>
          <button
            className={medCSS.buttonClass1}
            onClick={() => {
              handleADDgo();
            }}
          >
            ADD ITEM
          </button>
          <button
          className={medCSS.buttonClass1}
            onClick={() =>
              (document.getElementById("Medadditem").style.display = "none")
            }
          >
            close
          </button>
          </div>
          
        </div>

        <div className={medCSS.medHolder}>
          {IncomingMedicine.map((item) => (
            <MedSetting {...item}></MedSetting>
          ))}
        </div>
      </div>
    </div>
  );
};

export default InvManage;
const MedSetting = ({ id, Name, MafName, Price, Discount, Stock }) => {
  let linky = "/CRUDmed" + "?" + "id=" + id;
  return (
    <Link to={linky} style={{ width: "22%", margin: "0 15px" }}>
      <div className={medCSS.itemDetail}>
        <h2>{Name}</h2>
        <h3>{MafName}</h3>
        <h3>
          Rs <span>{Price}</span>,    { }
          <span class="margining" id="discount">
            {Discount}
            <span>% off</span>
          </span>
        </h3>
        <p>
          Stock: <span>{Stock}</span>
        </p>
      </div>
    </Link>
  );
};
