import React from 'react';
import { Link } from 'react-router-dom';
import '../Manager/manageOrder.css'
const ManageOrder = () => {
    let IncomingOrders = JSON.parse(localStorage.getItem('orderStock'))
    return (
        <div id='maindiv'>
            <nav id='manageordnav'>
                <div>
                    <h1 id="welcome">Welcome</h1>
                    <h3 id='name'>Shubham Upadhyay</h3>
                </div>
                <div id="desdiv">
                    <h3 id="des">Store Manager</h3>
                    <i class="fas fa-user-tie"></i>
                </div>
            </nav>
            <div id='content'>
                <h1>Manage Orders</h1>
                <Link to = '/neworder'><button class='buttonClass3'>NEW ORDER</button></Link>
                <div id='ordercontainer'>
                {IncomingOrders.map(item => <OrderSet {...item}></OrderSet>)}
               
                    
                </div>
            </div>
        </div>
    );
}

export default ManageOrder;
const OrderSet = ({id,name,mobile,products,total,createdby}) => {
    let linky = '/CRUDorder' +'?'+ 'id=' +id
    return ( 
      <Link to={linky} >  <div id='orderDetail'>
        <h3>Order No -<span>{id}</span></h3>
        <h3>Customer Name - <span>{name}</span></h3>
        <h3>Mob: {mobile}</h3>
        {products.map(item => <ProSet {...item}></ProSet>)}
        <h3>Total-<span>Rs. {total}</span></h3>
    </div> </Link>
     );
}
const ProSet = ({mediName,quantity}) => {
    return ( 
        <h3 id="product">
         <span>{mediName}</span><span>{quantity}</span>
        </h3>
     );
}
 