import React from 'react';
import { Link } from 'react-router-dom';
import { useLocation} from 'react-router';
import { useState } from 'react';
import '../Manager/CRUDorder.css'
const CRUDorder = (props) => {
    const [Refresh, setRefresh] = useState(0)
    const [da, setda] = useState([]);
    const medData = JSON.parse(localStorage.getItem('MedicineStock'))
    let id = useLocation().search
    const idp = new URLSearchParams(id).get('id')
    console.log(idp)
    const incoData = JSON.parse(localStorage.getItem('orderStock'))
    let temp;
    const data = () => {
        for (let i = 0; i < incoData.length; i++) {
            if (incoData[i].id == idp) {
                temp = incoData[i];
                break;
            }
        }
    }
    console.log(temp)
    const handleDelete = () => {
        let j;
        for (let i = 0; i < incoData.length; i++) {
            if (incoData[i].id == idp) {
                j = i;
                break;
            }
        }
        localStorage.setItem(
            "orderStock",
            JSON.stringify(
                incoData.slice(0, j).concat(incoData.slice(j + 1))
            )
        );
    }
    const handleUpdate = () => {
        let tempUpdate;
        let i;
        for (i = 0; i < incoData.length; i++) {
            if (incoData[i].id == idp) {
                tempUpdate = incoData[i];
                break;
            }
        }
        let obj1 = {
            id: tempUpdate.id,
            createdby: tempUpdate.createdby,
            products:da,
            mobile: document.getElementById("mobile").innerText,
            name: document.getElementById("Name").innerText,
            total: document.getElementById("total").innerText,
        }
        incoData[i] = obj1
        localStorage.setItem("orderStock", JSON.stringify(incoData))
    }
 const handletotalmed = () =>{
     let y=document.getElementsByClassName('dataget');
     let y1=y.length;
     let out=[]
     for(let i=0;i<y1;i++) out.push({mediName:y[i].children[0].innerHTML,quantity:y[i].children[3].innerHTML})
    console.log(out)
    setda(out)
        let j;
    for (let i = 0; i < incoData.length; i++) {
        if (incoData[i].id == idp) {
            j = i;
            break;
        }
    }
    incoData[j].products=out
    localStorage.setItem("orderStock",JSON.stringify(incoData))
    setRefresh(Refresh+1)
    document.getElementById('addMed').style.display='none'
 }

    return (
        <div id='maindiv'>
            {data()}
            <nav id='crudordnav'>
                <div>
                    <h1 id="welcome">Welcome</h1>
                    <h3 id='name'>Shubham Upadhyay</h3>
                </div>
                <div id="desdiv">
                    <h3 id="des">Store Manager</h3>
                    <i class="fas fa-user-tie"></i>
                </div>
            </nav>
            <div id='formdiv'>
                <h1>Manage Order</h1>
                <h4>Order Number <span id='orderNo'>{temp.id}</span></h4>
                <form>
                    <p id='Name' contentEditable='true'>{temp.name}</p>
                    <p id='mobile' contentEditable='true'>{temp.mobile}</p>
                    {temp.products.map(item => <ProSet {...item}></ProSet>)}
                    <p id='total'>{temp.total}</p>
                </form>
                <div id='buttonDiv'>
                    <Link to='/order' onClick={() => handleUpdate()}><button className='buttonClass4' id='update'>Update</button></Link>
                    <Link to='/order' onClick={() => handleDelete()}><button className='buttonClass4' id='delete'>Delete</button></Link>
                    <buttton id="addmed" className="buttonClass4" onClick={()=>{
                    document.getElementById('addMed').style.display='flex'
                    }}> Add more </buttton>
                </div>
            </div>
            <div id='addMed'>
                <h2>MEDICINE LIST</h2>
                <div id="hold">
                    {medData.map(item => <MedCard {...item}></MedCard>)}
                </div>
                <buttton id="addmed" className="buttonClass4"  onClick={()=>handletotalmed()}> Done</buttton>
            </div>
        </div>
    );
}

export default CRUDorder;
const ProSet = ({ mediName, quantity }) => {
    return (
        <h3 id="product">
            <span>{mediName}</span><span>{quantity}</span>
        </h3>
    );
}
const MedCard = ({ id, Name, Price }) => {
    return (
        <div id="medhold" className="dataget">
            <p>{Name}</p>
            <p>{Price}</p>
            <span id='minus' onClick={(e) => {
                let y = parseInt((e.target.parentElement.childNodes[3].childNodes[0].data));
                if(y>0)
                {y = y - 1;
                e.target.parentElement.childNodes[3].childNodes[0].data = y;}
                else{
                    alert('Cant be less than 0')
                }
            }}>-</span>
            <span>0</span>
            <span id='plus'onClick={(e) => {
                let y = parseInt((e.target.parentElement.childNodes[3].childNodes[0].data));
                y = y + 1;
                e.target.parentElement.childNodes[3].childNodes[0].data = y;
                console.log(document.getElementsByClassName('dataget'))
                console.log(document.getElementsByClassName('dataget')[0].children[0].innerHTML) 
                console.log(document.getElementsByClassName('dataget')[0].children[3].innerHTML)
                
            }}>+</span>
        </div>
    );
}